@font-face {
  font-family: "BebasNeue";
  font-style: normal;
  font-weight: 400;
  src: url(/sportska_academija/static/media/BebasNeue-Regular.895af0d7.ttf) format("truetype");
}

.bold-text {
    color: var(--main-text-color);
    font-weight: 600;
}
.yellow-text {
    color: var(--main-text-yellow);
    margin: 0;
    padding: 0;
    display: inline-block;
}
:root {
  --main-text-color: #FFFFFF;
  --main-text-yellow: #FFD600;
  --main-black: #000000;
}

.page {
  display: block;
  max-width: 100vw;
  margin: 0 auto;
  overflow: hidden;
}
.navigation {
    width: 100%;
    height: 100%;
    min-height: 80px;
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    padding-bottom: 42px;
    overflow: hidden;
}
.navigation__menu {
    --padding-top: 30px;
    display: flex;
    grid-gap: 100px;
    gap: 100px;
    padding-top: var(--padding-top);
    position: fixed;
    transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 1200px) {
    .navigation__menu {
        grid-gap: 50px;
        gap: 50px;
    }
}

@media screen and (max-width: 1000px) {
    .navigation__menu {
        flex-direction: column;
        padding: 15vh 0 0;
        grid-gap: 60px;
        gap: 60px;
        z-index: 2;
        color: var(--main-black);
    }

    .navigation__menu::before{
        content: '';
        position: absolute;
        width: 170vw;
        height: 140vh;
        top: calc(100vh - 100vh - var(--padding-top));
        left: calc(100vw - 150vw);
        background-color: rgba(128, 128, 128, 0.7);
        box-shadow: inset 0 0 150px 1500px rgba(0, 0, 0, 0.7);
    }
}
.navigation__home-icon {
    width: 80px;
    height: 80px;
    position: absolute;
    background-image: url(/sportska_academija/static/media/logo.0dae51d8.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 50%;;
    cursor: pointer;
    top: 30px;
    left: 80px;
    z-index: 2;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
}

.navigation__home-icon::before {
    content: '';
    width: 0;
    height: 0;
    position: inherit;
    top: 50%;
    left: 50%;
    box-shadow: 0 0 35px 35px var(--main-text-yellow);
    opacity: 0;
    z-index: 1;
    transition: all 0.3s ease-in-out;
}

.navigation__home-icon:hover::before {
    opacity: 0.8;
}
.navigation__menu-icon {
    width: 50px;
    height: 50px;
    position: absolute;
    background-size: cover;
    background-repeat: no-repeat;
    -webkit-filter: invert(100%) opacity(0.5);
            filter: invert(100%) opacity(0.5);
    cursor: pointer;
    top: 30px;
    right: 80px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    z-index: 2;
}

.navigation__menu-icon:hover {
    opacity: 0.8;
}
.navigation__menu-item {
    font-weight: 800;
    font-size: 20px;
    line-height: 36px;
    list-style: none;
    text-decoration: none;
    display: inline-block;
    position: relative;
    cursor: pointer;
    transition: color 0.3s ease-in-out;
    color: var(--main-text-color);
    z-index: 1;
}

.navigation__menu-item:hover {
    color: var(--main-black);
}

.navigation__menu-item::after {
    content: '';
    width: 0;
    height: 120%;
    position: absolute;
    bottom: -5px;
    left: 50%; 
    z-index: -1;
    display: block;
    border-radius: 10px;
    background-color: var(--main-text-yellow);
    transition: width 0.3s ease-in-out;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
}

.navigation__menu-item:hover::after {
    width: 120%;
}

@media screen and (max-width: 1000px) {
    .navigation__menu-item {
        /* font-size: 64px; */
        font-size: calc(4vh + 4vw);
    }

    .navigation__menu-item::after {
        height: 250%;
        bottom: -20px;
    }
}
.navigation__contact-btn {
    border: 10px var(--main-text-yellow) solid;
    border-radius: 15px;
    padding: 25px;
    white-space: nowrap;
    z-index: 2;
    color: var(--main-text-color);
    font-family: 'Inter';
    font-weight: 700;
    font-size: calc(2vw + 2vh);
    line-height: 24px;
    cursor: pointer;
    position: relative;
}

.navigation__contact-btn:hover {
    color: var(--main-black);
}

.navigation__contact-btn::after {
    content: '';
    width: 0;
    height: 100%;
    display: block;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 50%;
    background-color: var(--main-text-yellow);
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    transition: all 0.1s ease-in-out;
}

.navigation__contact-btn:hover::after {
    width: 100%;
}



@-webkit-keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(-360deg);
              transform: rotate(-360deg);
    }
  }

@keyframes spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(-360deg);
              transform: rotate(-360deg);
    }
  }

.loading {
    position: relative;
    top: 50%;
    left: 50%;
    max-width: 100px;
    max-height: 100px;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}
