    border: 2px solid var(--main-text-yellow);
    border-radius: 15px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
}

.contacts__form-btn:hover {
    box-shadow: 0 0 10px var(--main-text-yellow);
    color: var(--main-text-color);
}

.popup {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    background-color: rgba(0, 0, 0, 0.75);
    box-sizing: border-box;
    padding: 0 19px;
    /* visibility: hidden; */
    /* opacity: 0; */
    transition: all 0.5s ease;
    cursor: pointer;
  }
.popup__body {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
}
/* .popup__close-btn {
    background-image: url("../../../../images/Close_Icon.svg");
    position: absolute;
    top: 0;
    right: 0;
} */

.popup__close-btn {
    background-image: url(/sportska_academija/static/media/Close_Icon.4c6a2169.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-color: transparent;
    width: 32px;
    height: 32px;
    border: none;
    position: absolute;
    top: 0;
    right: -50px;
    cursor: pointer;
    z-index: 3;
  }
  
  .popup__close-btn:hover {
    opacity: 0.6;
  }
  
  @media screen and (max-width: 590px) {
    .popup__close-btn {
      background-size: 20px 20px;
      right: 0px;
    }
  }
  
.popup__content {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}
.popup__img {
    width: 60vw;
    height: auto;
    border-radius: 1rem;
}
.popup_opened {
  /* visibility: visible;
  opacity: 1; */
}


